@import './styles/functions.scss';
@import './styles/spritesheets.scss';

.App {
  display: grid;
  grid-template-rows: 3rem 3rem auto;
  text-align: center;
  height: 100vh;
  row-gap: 2rem;
  > * {
    position: relative;
  }
}

.ch-sprite {
    @include sprite-builder(
        map-get($characters, "prom"),
        var(--character-sprites),
        0.1,
        "ch"
    );
}

.ui-sprite {
    @include sprite-builder(
    $ui,
    var(--img),
    0.25,
    "ui"
  );
}